import style from './Theme.module.scss'

export const Theme = ({callback, theme}) => {


    return (
        <div className={style.theme_wrapper}>
            <div className={style.container}>
                <span className={style.icons} style={{ color: theme ? "grey" : "#ff9800" }}>☀</span>
                <div>
                    <label className={style.switch}>
                        <input type="checkbox" onChange={() => callback()} checked={theme}/>
                        <span className={style.slider}> </span>
                    </label>
                </div>
                <span className={style.icons} style={{ color: theme ? "#1565c0" : "grey" }}>☽</span>
            </div>
        </div>

    )


}
